<template>
    <v-container>
        <h2>Current Price: {{ this.stock.each_frame.Price | toCurrency() }}</h2>
        <h2>Recommended Order Size: {{ getSizeAmount(this.stock.each_frame.Price) }}</h2>
        <v-switch
            :input-value="!this.switch"
            @change="tradeSide"
        ></v-switch>
        <!-- <v-btn @click="sendOrder()">Place Paper Order</v-btn> -->
    </v-container>
</template>

<script>
export default {
    props: ['stock'],
    data() {
        return {
            switch: true,
            orderInfo: {
                stock: this.stock.each_frame.id,
                quantity: 2,
                side: 'buy',
                type: 'limit',
                time_in_force: 'day',
                limit_price: 380.66,
            }
        }
    },
    mounted() {

    },
    methods: {
        sendOrder() {
            this.$store.dispatch('createOrder', this.orderInfo)
        },
        getSizeAmount(price) {
            var adj_price = Math.round((price + Number.EPSILON) * 100) / 100

            // console.log(adj_price);
            var size = Math.floor(1000 / adj_price);
            return size
        },
        tradeSide() {
            console.log(this.orderInfo.side)
            this.switch == !this.switch
            if (this.switch == true) {
                this.orderInfo.side == 'buy'
            } else {
                this.orderInfo.side == 'sell'
            }

        }
      
    },
    filters: {
        toCurrency(amount) {
        var formatter = new Intl.NumberFormat('en-US', {
            style: 'currency',
            currency: 'USD',
        });
        return formatter.format(amount)
        }
    },
    
}
</script>

